.blog-content-screen-container {
    background-color: white;
    width: fit-content;
    height: fit-content;
    min-height: 100vh;
    min-width: 100vw;

    .article-embed-youtube-video {
        width: calc(100%);
        height: 480px !important;
        margin-top: 15px;

        @media screen and (min-width: 1400px) {
            height: 480px !important;
        }

        @media screen and (min-width: 1200px) and (max-width: 1400px) {
            height: 420px !important;
        }

        @media screen and (min-width: 992px) and (max-width: 1200px) {
            height: 348px !important;
        }

        @media screen and (min-width: 768px) and (max-width: 992px) {
            height: 400px !important;
        }

        @media screen and (min-width: 576px) and (max-width: 768px) {
            height: 300px !important;
        }

        @media screen and (max-width: 576px) {
            width: calc(100vw - 30px);
            height: calc((100vw - 30px) * (9 / 16)) !important;
        }
    }

    .embed-cover-photo {
        width: 100%;
        height: auto;
        margin-top: 15px;
        object-fit: contain;
    }

    .blog-content-screen-container {
        .cover-container {
            padding: 0px !important;
            height: 300px;
            display: flex;

            .black-overlay {
                width: 100%;
                position: absolute;
                background: rgba(0, 0, 0, 0.65);
                height: 300px;
                z-index: 99;
            }

            img {
                width: 100%;
                object-position: center;
                height: 300px;
                object-fit: cover;
                align-items: center;
                z-index: 98;
            }

            h1.article-title {
                display: flex;
                text-align: center;
                font-family: inherit;
                font-size: 35px;
                font-weight: bold;
                justify-content: center;
                align-items: center;
                position: absolute;
                width: 100%;
                height: 300px;
                z-index: 100;
                color: white;
            }
        }

        .post-and-side-container-fluid {
            background-color: white;
            min-height: 100vh;
            width: 100vw;

            .post-and-side-container {
                height: 100%;
                padding: 0px !important;

                .post-and-side-row {
                    .post-container {
                        border-right: 1.5px solid lightgray;
                        background-color: white;
                        padding: 45px 30px 30px 15px !important;

                        @media screen and (max-width: 576px) {
                            padding: 0px 30px 30px 15px !important;
                        }

                        @media screen and(max-width: 1200px) {
                            border-right-width: 0px;
                        }

                        .image-container {
                            width: 100%;
                            margin-top: 15px;
                            border: 3px solid black;

                            img {
                                width: 100%;
                                object-fit: cover;
                            }
                        }

                        .category-container {
                            color: rgb(112, 112, 112);
                            height: 40px;
                            display: flex;
                            align-items: flex-end;
                            letter-spacing: 0.5px;
                            font: normal 16px/20px HeronSansCondMedium;
                        }

                        .title-container {
                            letter-spacing: 0.3px;
                            font: 700 42px/54px HeronSansCondBold;
                            margin-bottom: 10px;

                            @media screen and (max-width: 576px) {
                                font: 700 33px/42px HeronSansCondBold;
                            }
                        }

                        .subtitle-container {
                            font: 500 30px/36px HeronSansCondMedium;
                            color: #585757;
                            margin-bottom: 15px;
                        }

                        .date-container {
                            font: 300 20px/24px HeronSansCond;
                            color: #9b9b9b;
                            margin-bottom: 15px;
                        }

                        article {
                            font: 400 21px/29px CompactDisplayMedium;
                            color: rgb(0, 0, 0);

                            a {
                                color: rgb(68, 68, 68);
                                font-weight: bold;
                            }

                            figure {
                                &:first-of-type {
                                    display: none;
                                }

                                img {
                                    width: 100%;
                                    object-fit: contain;
                                    display: block;
                                }
                            }

                            h1 {
                                font: 500 40px/45px HeronSansCondMedium;
                                color: #585757;

                                &:first-child {
                                    display: none;
                                }
                            }

                            h2 {
                                font: 500 35px/40px HeronSansCondMedium;
                                color: #585757;

                                &:first-of-type {
                                    display: none;
                                }
                            }

                            h3 {
                                font: 500 30px/35px HeronSansCondMedium;
                                color: #585757;

                                &:first-of-type {
                                    display: none;
                                }
                            }

                            p {
                                color: gray;
                                font: inherit;
                            }

                            strong {
                                color: black;
                                font: inherit;
                                font-weight: bold;
                            }

                            pre {
                                padding: 15px;
                                overflow: scroll;
                                background-color: black;
                                height: fit-content;
                                max-width: 100%;
                                color: white;
                                //-ms-overflow-style: none;
                                /* IE and Edge */
                                scrollbar-width: 1px;

                                /* Firefox */
                                &::-webkit-scrollbar {
                                    display: none;
                                    /* for Chrome, Safari, and Opera */
                                }
                            }
                        }
                    }

                    .side-container {
                        min-height: 100vh;
                        background-color: white;
                        padding: 15px 30px 15px 30px !important;

                        .side-container-education-promotion-container {

                            .side-container-education-promotion {
                                margin-top: 45px;
                                box-shadow: -1px -1px 12px -1px rgba(0, 0, 0, 0.28);
                                -webkit-box-shadow: -1px -1px 12px -1px rgba(0, 0, 0, 0.28);
                                -moz-box-shadow: -1px -1px 12px -1px rgba(0, 0, 0, 0.28);


                                .side-container-education-promotion-cover {
                                    width: 100%;
                                    height: auto;
                                    object-fit: contain;
                                }

                                h3 {
                                    color: black;
                                    text-decoration: none;
                                    text-align: center;
                                    margin-top: 30px;
                                    font: 700 25px/25px HeronSansCondLight;
                                }

                                .side-container-education-promotion-icon {
                                    width: 100%;
                                    height: 55px;
                                    text-align: center;
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;
                                    margin-bottom: 30px;

                                    i {
                                        color: black;
                                    }
                                }
                            }
                        }

                        a {
                            text-decoration: none !important;

                            &:hover {
                                text-decoration: underline !important;
                                color: black;
                            }

                            .post-card {
                                margin-top: 45px;
                                box-shadow: -1px -1px 12px -1px rgba(0, 0, 0, 0.28);
                                -webkit-box-shadow: -1px -1px 12px -1px rgba(0, 0, 0, 0.28);
                                -moz-box-shadow: -1px -1px 12px -1px rgba(0, 0, 0, 0.28);

                                .image-container {
                                    width: 100%;
                                    height: auto;
                                    object-fit: contain;
                                    border-bottom: 15px solid red;
                                }

                                .title-container {
                                    padding: 0px 15px;
                                    max-height: 140px;
                                    text-align: center;
                                    display: flex;
                                    padding-bottom: 15px;

                                    h4 {
                                        margin: auto;
                                        overflow: hidden;
                                        text-overflow: ellipsis;
                                        -webkit-line-clamp: 3;
                                        -webkit-box-orient: vertical;
                                        display: -webkit-box;
                                        line-height: 1.5;
                                        margin-top: 20px;
                                        max-height: 140px;
                                        background-color: white;
                                        z-index: 9999;
                                        color: black !important;
                                        padding: 0px 10px;
                                        align-items: center;
                                        justify-content: center;
                                        font: 500 21px/32px HeronSansCond;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}